*,
:before,
:after {
  line-height: inherit;
  margin: 0;
  padding: 0;
  color: inherit;
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  background-color: #fff;
  min-width: 300px;
  line-height: 1;
  font-family: 'Crimson Text', serif;
  font-size: 16px;
  color: #4a4e57;
}
h1,
h2,
h3,
h4,
h5 {
  font-family: 'Libre Franklin', sans-serif;
  color: #1d222e;
}
h1 {
  font-weight: 700;
  font-size: 38px;
}
h2 {
  font-weight: 300;
  font-size: 28px;
  text-transform: uppercase;
}
h3 {
  font-weight: 300;
  font-size: 21px;
}
h4 {
  font-weight: 700;
  font-size: 16px;
}
h5 {
  font-weight: 400;
  font-size: 10px;
  text-transform: uppercase;
}
small {
  font-size: 13px;
}
.text-block {
  margin-bottom: 25px;
}
.text-block h1 {
  margin-bottom: 25px;
}
.text-block h2,
.text-block h3 {
  margin-bottom: 15px;
}
.text-block p {
  line-height: 1.5;
  margin-bottom: 10px;
}
.text-block .quote {
  font-size: 13px;
  color: #a4a6ab;
}
[grid] {
  display: flex;
}
[grid] [column-width] {
  flex: 1;
}
[grid] [column-width="1"] {
  width: 8.333333333333332%;
  max-width: 8.333333333333332%;
}
[grid] [column-width="2"] {
  width: 16.666666666666664%;
  max-width: 16.666666666666664%;
}
[grid] [column-width="3"] {
  width: 25%;
  max-width: 25%;
}
[grid] [column-width="4"] {
  width: 33.33333333333333%;
  max-width: 33.33333333333333%;
}
[grid] [column-width="5"] {
  width: 41.66666666666667%;
  max-width: 41.66666666666667%;
}
[grid] [column-width="6"] {
  width: 50%;
  max-width: 50%;
}
[grid] [column-width="7"] {
  width: 58.333333333333336%;
  max-width: 58.333333333333336%;
}
[grid] [column-width="8"] {
  width: 66.66666666666666%;
  max-width: 66.66666666666666%;
}
[grid] [column-width="9"] {
  width: 75%;
  max-width: 75%;
}
[grid] [column-width="10"] {
  width: 83.33333333333334%;
  max-width: 83.33333333333334%;
}
[grid] [column-width="11"] {
  width: 91.66666666666666%;
  max-width: 91.66666666666666%;
}
[grid] [column-width="12"] {
  width: 100%;
  max-width: 100%;
}
.constrain {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}
.style-guide .guide-header {
  background-color: #dbf0f4;
  padding: 25px 0;
}
.style-guide .guide-header .header-logo {
  display: block;
  background-repeat: no-repeat;
  width: 379px;
  height: 50px;
}
@media (max-width: 409px) {
  .style-guide .guide-header .header-logo {
    background-size: 100%;
    width: 100%;
    max-width: 379px;
    height: 0;
    padding-bottom: 13.192612137203167%;
  }
}
@media only screen and (max-device-pixel-ratio: 1.99), only screen and (max-resolution: 191dpi), only screen and (max-resolution: 1.99dppx) {
  .style-guide .guide-header .header-logo {
    background-image: url("./guide-logo.png");
  }
}
@media only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx) {
  .style-guide .guide-header .header-logo {
    background-image: url("./guide-logo@2x.png");
    background-size: contain;
  }
}
.style-guide main {
  padding-top: 60px;
}
.style-guide .guide-section-heading {
  margin-bottom: 15px;
  color: #777a81;
}
@media (max-width: 599px) {
  .style-guide .guide-headings {
    display: none;
  }
}
@media (min-width: 600px) {
  .style-guide .guide-headings .headings > * {
    display: block;
    margin-bottom: 15px;
  }
}
